input {
    margin-bottom: 30px;
    height: 36px;
    width: 440px;
    color: aliceblue;
    font-size: 22px;
    border: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.895);
    background-color: rgba(255, 255, 255, 0.1)
}

input::placeholder {
    color: rgba(249, 249, 249, 0.685);
    padding-left: 10px;
}

input:focus {
    background-color: rgba(0, 0, 0, 0.573);
}


@keyframes slideme {
    0% {
        left: -30px;
        margin-left: 0px;
    }

    30% {
        left: 110%;
        margin-left: 80px;
    }

    100% {
        left: 110%;
        margin-left: 80px;
    }
}

.btn {
    width: 159px;
    height: 42px;
    overflow: hidden;
    position: relative;
    background-color: rgb(194, 137, 255);
    border-radius: 10px;
    font-weight: 600;
    margin-left: 130px;

}

.btn:hover {
    background-color: rgb(122, 68, 173);
    color: aliceblue;
}

.btn:after {
    content: "";
    display: block;
    width: 30px;
    height: 200px;
    margin-left: 60px;
    background: rgba(238, 239, 255, 0.817);
    opacity: 0.5;
    position: absolute;
    left: -40px;
    top: -100px;
    z-index: 1;
    transform: rotate(45deg);
    transition: all 0.1s;
    animation-name: slideme;
    animation-duration: 3s;
    animation-delay: 0.05s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
}